import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ResponseAvailableProduct } from '@api/models/responseAvailableProduct';
import { Endpoints } from '@api/constants/endpoints.constants';
import { map, pluck } from 'rxjs/operators';
import { SalesforceResponse, Cnogroupcnocnae, ImpositionCondition } from '@api/models';
import { AddressResponseData } from '@api/models/addressResponseData';

@Injectable({
  providedIn: 'root',
})
export class DictionaryService {
  constructor(private http: HttpClient) {}

  getProducts(email?: string) {
    const params = email && { email };

    return this.http
      .get<ResponseAvailableProduct>(Endpoints.products, { params })
      .pipe(map(({ data }) => data[0]));
  }

  getEmploymentStatus() {
    return this.http
      .get<SalesforceResponse<Cnogroupcnocnae>>(Endpoints.employmentStatus)
      .pipe(pluck('data'));
  }

  getCNO() {
    return this.http.get<SalesforceResponse<Cnogroupcnocnae>>(Endpoints.cno).pipe(pluck('data'));
  }

  getCNAE() {
    return this.http.get<SalesforceResponse<Cnogroupcnocnae>>(Endpoints.cnae).pipe(pluck('data'));
  }

  getLocation(postalcode: string) {
    const params = { postalcode };
    return this.http
      .get<SalesforceResponse<AddressResponseData>>(Endpoints.address, { params })
      .pipe(
        map((response) => {
          return response.data[0].availableLocation;
        })
      );
  }

  getImpositionConditions() {
    return this.http
      .get<SalesforceResponse<ImpositionCondition>>(Endpoints.impositionCondition)
      .pipe(pluck('data'));
  }
}
