import { CasePayload } from '@api/models/case-payload.model';
import { Injectable } from '@angular/core';
import { SalesforceResponse, Case, CaseRequest } from '@api/models';
import { pluck } from 'rxjs/operators';
import { environment } from '@environment/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class CaseService {
  constructor(private httpClient: HttpClient) {}

  getCases() {
    const url = `${environment.apiBasePath}${environment.endpoints.case}`;
    return this.httpClient.get<SalesforceResponse<Case>>(url).pipe(pluck('data'));
  }

  createCase(body: { caseList: CasePayload[] }) {
    const url = `${environment.apiBasePath}${environment.endpoints.case}`;
    return this.httpClient.post<any>(url, body);
  }
}
