const DNI_REGEX = /^(\d{8})([A-Z])$/;
const CIF_REGEX = /^([ABCDEFGHJKLMNPQRSUVW])(\d{7})([0-9A-J])$/;
const NIE_REGEX = /^[XYZ]\d{7,8}[A-Z]$/;

export function validateSpanishID(str: string) {
  str = str.toUpperCase();

  let valid = false;
  const type = spainIdType(str);

  switch (type) {
    case 'dni':
      valid = validDNI(str);
      break;
    case 'nie':
      valid = validNIE(str);
      break;
    case 'cif':
      valid = validCIF(str);
      break;
  }

  return {
    type,
    valid,
  };
}

function spainIdType(str: string) {
  if (str.match(DNI_REGEX)) {
    return 'dni';
  }
  if (str.match(CIF_REGEX)) {
    return 'cif';
  }
  if (str.match(NIE_REGEX)) {
    return 'nie';
  }
}

function validDNI(dni: string) {
  const dniLetters = 'TRWAGMYFPDXBNJZSQVHLCKE';
  const letter = dniLetters.charAt(parseInt(dni, 10) % 23);

  return letter === dni.charAt(8);
}

function validNIE(nie: string) {
  let niePrefix: number;

  switch (nie.charAt(0)) {
    case 'X':
      niePrefix = 0;
      break;
    case 'Y':
      niePrefix = 1;
      break;
    case 'Z':
      niePrefix = 2;
      break;
  }

  return validDNI(niePrefix + nie.substr(1));
}

function validCIF(cif: string) {
  const match = cif.match(CIF_REGEX);
  const [_, letter, num, control] = match;

  let evenSum = 0;
  let oddSum = 0;
  let n: number;

  for (let i = 0; i < num.length; i++) {
    n = parseInt(num[i], 10);
    if (i % 2 === 0) {
      n *= 2;
      oddSum += n < 10 ? n : n - 9;
    } else {
      evenSum += n;
    }
  }

  const controlDigit = 10 - parseFloat((evenSum + oddSum).toString().substr(-1));
  const controlLetter = 'JABCDEFGHI'.substr(controlDigit, 1);

  if (letter.match(/[ABEH]/)) {
    return parseFloat(control) === controlDigit;
  } else if (letter.match(/[KPQS]/)) {
    return control === controlLetter;
  } else {
    return parseFloat(control) === controlDigit || control === controlLetter;
  }
}
