import { InjectionToken } from '@angular/core';
import { environment } from '@environment/environment';

const baseUrl = `${environment.applications.documents}/imagenes`;

export const banners = {
  globalPosition: `${baseUrl}/banner_deposito.jpg`,
  deposit: `${baseUrl}/img-deposit.jpg`,
  transactions: `${baseUrl}/img-transactions.jpg`,
  transfer: `${baseUrl}/img-transfer.jpg`,
};

export type Banners = typeof banners;

export const BANNERS = new InjectionToken<Banners>('BANNERS', {
  providedIn: 'root',
  factory: () => banners,
});
